@import url('https://fonts.googleapis.com/css?family=Ubuntu');
@import url('https://fonts.googleapis.com/css?family=Noto+Sans');
@import url('https://fonts.googleapis.com/css?family=Passions+Conflict');

.section {
  color: #ebdbb2;
  font-family: 'Noto Sans';
  padding: 20px;
}

#buttonsContainer {
  display: flex;
  justify-content: center;
  margin: auto;
  text-align: center;
  padding-bottom: 40px;
}


button {
  width: 250px;
  height: 90px;
  border-width: 0;
  border-radius: 20px;
  box-shadow: 0px 0px 2px 4px #83a598;
  margin: 20px;
  color: #ebdbb2;
  background-color: #17191a;
  font-family: 'Ubuntu';
  font-size: 2rem;
  font-weight: 100;
  overflow: hidden;
  padding: 1rem 2rem;
  position: relative;
  text-decoration: none;
  transition: .2s transform ease-in-out;
  will-change: transform;
  z-index: 0;
  
} 
button::after {
  background-color:#ebdbb2;
  border-radius: 20px;
  content: '';
  display: block;
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  transform: translate(-100%, 0) rotate(10deg);
  transform-origin: top left;
  transition: .2s transform ease-out;
  will-change: transform;
  z-index: -1;
}
button:hover::after {
    transform: translate(0, 0);
}
    
button:hover {
    box-shadow: 0px 0px 0px 0px #83a598;
    border: 2px solid transparent;
    color: #17191a;
    transform: scale(1.05);
    will-change: transform;
}
button:active {
  color: #839fa5;
  transform: scale(1.1);
  will-change: transform;
}

#pageContent {
  font-size: 1.4rem;
  line-height: 3rem;
  display: block;
  align-items: center;
  top: 600px;
  width: 60%;
  padding: 20px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  background-color: #17191a;
  border-radius: 20px;
  box-shadow: 0px 0px 4px 7px #83a598;
}
#pageContent p {
  background: linear-gradient(
    to right, 
    #ebdbb2, 
    #839fa5
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.pageTitle {
  animation: fadeIn 1.5s;
  text-align: center;
  padding: 30px;
  margin: 0;
  color: #ebdbb2;
  background: -webkit-linear-gradient(#ebdbb2, #83a598);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  line-height: 20px;
  font-size: 6rem;
  line-height: 60px;
  font-family: 'Passions Conflict', cursive;
}

.projectImage,
.projectInfo {
  display:inline-block;
  vertical-align:middle;
}

.projectImage {
  animation: fadeIn 1.5s;
  width: 40%;
  border-radius: 20px;
  padding: 10px;
}

.projectInfo {
  border-top: 30px;
  width: 50%;
  text-align: center;
  color: #ebdbb2;
}
.projectInfo p {
  font-size: 1.2rem;
}

.projectLink {
  color: #ebdbb2;
  text-decoration: underline;
}

.pageSection {
  animation: fadeIn 0.1s;
}

#contactPage, 
#myProjectsPage {
  display: none;
}

@media (max-width: 950px) {
  #buttonsContainer {
    display: grid;
  }
  #pageContent {
    width: 80%;
  }
}

@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

